    created: function () {
      var query = this.$route.query;
      console.log("create good page");
      this.goods_id = query.goods_id;
      this.item_index = query.item_index;
      this.init_goods_page(query);
    },
    methods: {
      show:function(index){
        console.log("展示");
        console.log(this.list);
        this.$refs.previewer.show(index);
      },
      collapse:function(index){
        this["collapse"+index]=!this["collapse"+index];
      },
      init_goods_page: function (query) {
         var self=this;
          // api.getGoodsData({relations: ["image_attach", "images","mechanics","goods_ports","assemblies","standardfits","electrics"], parameters:query}).then((res)=>{
            var product_data=self.product_data.data;
            var goods_data=self.goods_data.data;
            self.handle_goods_data(product_data,goods_data);
           
          // });
      },
      handle_goods_data:function(product_data,goods_data){
        var self=this;
        // console.log(data);
        
        console.log(product_data);
        console.log(goods_data);
        var size_arr={};
        var color_arr={};
        var temp_arr=[];
        var total_store=0;
        for(var i=0;i<product_data.length;i++){
          var temp_obj=product_data[i].spec_desc.spec_value;
          temp_obj.product_id=product_data[i].product_id;
          temp_obj.goods_id=product_data[i].goods_id;
          temp_obj.bn=product_data[i].bn;
          temp_obj.price=product_data[i].price;
          temp_obj.bn=product_data[i].bn;
          temp_obj.store=product_data[i].store;
          temp_arr.push(temp_obj);
          (!!size_arr[temp_obj["1"]])?size_arr[temp_obj["1"]]++:size_arr[temp_obj["1"]]=1;
          (!!color_arr[temp_obj["2"]])?color_arr[temp_obj["2"]]++:color_arr[temp_obj["2"]]=1;
          total_store+=parseInt(product_data[i].store);
        }
        console.log("________________________");
        console.log(temp_arr);
        console.log(size_arr);
        console.log(color_arr);
        console.log(total_store);
        this.size_arr=size_arr;
        this.color_arr=color_arr;
        setTimeout(function(){
          self.show_previewer();
        },2000)
      },  
      show_previewer:function(){
        var self=this;
        console.log($(".goods-desc img"));
        self.list=[];
        $(".goods-desc img").addClass("previewer-demo-img");
        $(".vux-swiper img").addClass("previewer-demo-img");
        $.each($(".previewer-demo-img"),function(i,n){
          var obj={
            src:$(this).attr("src"),
            w:($(this).width()+8)*2,
            h:$(this).height()*2
          };
          self.list.push(obj);
          // $(this).addClass("previewer-demo-img");
          $(this).on("click",function(){
            console.log(i);
            self.show(i);
          })
        })
        console.log(self.list);
      },
      toggle_popup_show:function(){
        this.popup_show=!this.popup_show;
      }
    },
   
    components: {
      Swiper,
      Flexbox,
      FlexboxItem,
      XButton,
      Tab,
      TabItem,
      SwiperItem,
      Group,
      Cell,
      VueQArt,
